<template>
  <v-card v-if="gif" class="overflow-hidden">
    <v-app-bar color="#333" dark>
      <v-app-bar-title>Gifs</v-app-bar-title>

      <v-spacer></v-spacer>

      <v-btn
        class="link"
        @click="
          $router.push({
            name: 'home',
          })
        "
        >View All</v-btn
      >
    </v-app-bar>
    <v-sheet>
      <v-container>
        <v-row dense>
          <v-col>
            <v-img :src="gif.gif_url" width="100%"> </v-img>
          </v-col>
        </v-row>
      </v-container>
    </v-sheet>
  </v-card>
</template>

<script>
import axios from "axios";

export default {
  name: "Single",

  data: () => ({
    api: "v1/gifs/",
    api_key: "bHXi6e54UeAetadh1gr7DAe5QnOG0Fr0",
    gif: null,
    axios: axios.create({
      baseURL: "https://api.giphy.com",
    }),
  }),

  async mounted() {
    await this.getGifs();
  },
  methods: {
    async getGifs() {
      const response = await this.axios.get(this.api + this.$route.params.id, {
        params: {
          api_key: this.api_key,
        },
      });

      this.gif = {
        id: response.data.data.id,
        gif_url: response.data.data.images.downsized.url,
      };
      console.log(this.gif);
      return this.gif;
    },
  },
};
</script>
